import { notFound } from 'next/navigation';

export default async function OrderDetailPage({ params }) {
  const { id } = params;

  const res = await fetch(`http://localhost:3000/api/orders/${id}`);
  const data = await res.json();

  if (!data.success) return notFound();

  const order = data.order;

  return (
    <div className="p-6">
      <h1 className="text-2xl font-bold mb-4">Order Detail</h1>

      <div className="border p-4 rounded bg-white shadow-sm mb-6">
        <p><strong>Order ID:</strong> {order._id}</p>
        <p><strong>Status:</strong> {order.status}</p>
        <p><strong>Total Price:</strong> {order.totalPrice.toLocaleString()} Toman</p>
        <p><strong>Payment Method:</strong> {order.paymentMethod}</p>
        <p><strong>Created At:</strong> {new Date(order.createdAt).toLocaleString()}</p>
      </div>

      <h2 className="text-xl font-semibold mb-2">Products:</h2>
      <div className="space-y-4">
        {order.items.map((item, idx) => (
          <div key={idx} className="border rounded p-3 bg-gray-50">
            <img src={item.imageUrl} alt={item.name} className="w-24 h-24 object-cover mb-2" />
            <p><strong>Name:</strong> {item.name}</p>
            <p><strong>Quantity:</strong> {item.quantity}</p>
            <p><strong>Price:</strong> {item.price.toLocaleString()} Toman</p>
          </div>
        ))}
      </div>
    </div>
  );
}
